.projects-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
}

.project{
    flex: auto;
    margin: 1rem;
    text-align: center;
    > img{
        max-width: 150px;
        height: auto;
    }
}

.project-item {
    flex: 0 1 calc(50% - 2rem - 4px);
    background: $shark;
    color: $primary-color;
    margin: 1rem;
    text-align: center;
    position: relative;
    height: 200px;
    background-position: center center;
    background-size: cover;

    &:hover {
        .project-overlay {
            transform: scale(1);
        }
    }
}

.project-overlay {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    transition: all 0.5s cubic-bezier(.78,.14,.15,.86);
    padding: 20px;
    transform: scale(0);
    transform-origin: center center;
    background: rgba(0,0,0,0.8);
    background-clip: content-box;
    z-index: 2;
}

.project-title{
    position: absolute;
    top:50%;
    left:0;
    right:0;
    margin: 0 auto;
    transform: translateY(-50%);
}


@media all and (max-width: $mobile){
    .project-item{
        flex-basis: 100%;
    }
    .project{
        > img {
            max-width: 100px;
        }
    }
}